@charset "UTF-8";
.js .pages-stack {
  z-index: 100;
  pointer-events: none;
  -webkit-perspective: 1200px;
  perspective: 1200px;
  -webkit-perspective-origin: 50% -50%;
  perspective-origin: 50% -50%;
  height: 100vh;
}

.js .page {
  position: relative;
  z-index: 5;
  overflow: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100vh;
  pointer-events: auto;
  background: #fff !important;
  box-shadow: 0 -1px 10px rgba(0, 0, 0, 0.1);
}

.js .pages-stack--open .page {
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.45s, opacity 0.45s;
  transition: transform 0.45s, opacity 0.45s;
  -webkit-transition-timing-function: cubic-bezier(0.6, 0, 0.4, 1);
  transition-timing-function: cubic-bezier(0.6, 0, 0.4, 1);
}

/* Made with http://icomoon.io/ */
/* Resets */
*,
*:after,
*:before {
  box-sizing: border-box;
}

/* Helper classes */
.cf:before,
.cf:after {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

/* Main styles */
body {
  font-family: "Avenir Next", Avenir, "Helvetica Neue", "Lato", "Segoe UI", Helvetica, Arial, sans-serif;
  margin: 0;
  color: #cecece;
  background: #1d1e21;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #5c5edc;
  outline: none;
}

a:hover {
  color: #fff;
}

/* Blueprint header */
.bp-header {
  padding: 6em 10vw 2em;
}

.bp-header__title {
  font-size: 2.125em;
  font-weight: 700;
  line-height: 1.3;
  margin: 0;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #fff;
}

.bp-header__desc {
  font-size: 0.95em;
  margin: 0.5em 0 1em;
  padding: 0;
}

.bp-header__present {
  font-size: 0.85em;
  font-weight: 700;
  position: relative;
  z-index: 100;
  display: block;
  padding: 0 0 0.6em 0.1em;
  letter-spacing: 0.5em;
  text-transform: uppercase;
  color: #5c5edc;
}

.bp-tooltip:after {
  font-size: 50%;
  font-size: 75%;
  position: relative;
  top: -8px;
  left: -12px;
  width: 30px;
  height: 30px;
}

.bp-tooltip:hover:before {
  content: attr(data-content);
  font-size: 110%;
  font-weight: 700;
  line-height: 1.2;
  position: absolute;
  left: auto;
  margin-left: 4px;
  padding: 0.8em 1em;
  text-align: left;
  text-indent: 0;
  letter-spacing: 0;
  text-transform: none;
  color: #fff;
  background: #5c5edc;
}

.bp-nav {
  margin: 0 0 0 -0.75em;
}

.bp-nav__item {
  position: relative;
  display: inline-block;
  width: 2.5em;
  height: 2.5em;
  margin: 0 0.1em;
  text-align: left;
  border-radius: 50%;
}

.bp-nav__item > span {
  display: none;
}

.bp-nav__item:hover:before {
  content: attr(data-info);
  font-size: 0.85em;
  font-weight: bold;
  position: absolute;
  top: 120%;
  left: 0;
  width: 600%;
  text-align: left;
  pointer-events: none;
  color: #595a5f;
}

.bp-nav__item:hover {
  background: #5c5edc;
}

.bp-icon:after {
  font-family: "bpicons";
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  text-align: center;
  text-transform: none;
  color: #5c5edc;
  -webkit-font-smoothing: antialiased;
  speak: none;
}

.bp-nav .bp-icon:after {
  line-height: 2.4;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-indent: 0;
}

.bp-nav a:hover:after {
  color: #fff;
}

.bp-icon--next:after {
  content: "";
}

.bp-icon--drop:after {
  content: "";
}

.bp-icon--archive:after {
  content: "";
}

.bp-icon--about:after {
  content: "";
}

.bp-icon--prev:after {
  content: "";
}

@media screen and (max-width: 40em) {
  .bp-header {
    padding: 50px 20px 1em;
  }

  .bp-header__title {
    font-size: 1.8em;
  }
}
html.js,
.js body {
  overflow: hidden;
  height: 100vh;
}

/* Pages nav */
.pages-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  padding: 20px;
  text-align: center;
  background: #0e0f0f;
}

.js .pages-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50vh;
  padding: 30px;
  pointer-events: none;
  opacity: 0;
  background: transparent;
  -webkit-transition: -webkit-transform 1.2s, opacity 1.2s;
  transition: transform 1.2s, opacity 1.2s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  -webkit-transform: translate3d(0, 150px, 0);
  transform: translate3d(0, 150px, 0);
}

.js .pages-nav--open {
  pointer-events: auto;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.pages-nav__item {
  width: 33%;
  padding: 1em;
}

.js .pages-nav__item {
  padding: 0 10%;
}

.pages-nav .pages-nav__item--social {
  width: 100%;
  opacity: 0;
  -webkit-transition: -webkit-transform 1.2s, opacity 1.2s;
  transition: transform 1.2s, opacity 1.2s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
}

.pages-nav--open .pages-nav__item--social {
  opacity: 1;
  -webkit-transition-delay: 0.35s;
  transition-delay: 0.35s;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.link {
  font-size: 0.85em;
  padding: 40px;
  font-weight: bold;
  position: relative;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.link:hover,
.link:focus {
  color: #fff;
}

.link--page {
  display: block;
  color: #cecece;
  padding: 40px;
}

.link--page:not(.link--faded)::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 30px;
  height: 2px;
  background: #fff;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  -webkit-transform: scale3d(0, 1, 1);
  transform: scale3d(0, 1, 1);
}

.link--page:hover:before {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
}

.link--faded {
  color: #4f4f64;
}

.link--faded:hover,
.link--faded:focus {
  color: #5c5edc;
}

.link--page.link--faded {
  font-size: 0.65em;
}

.link--social {
  font-size: 1.5em;
  margin: 0 0.75em;
}

.text-hidden {
  position: absolute;
  display: block;
  overflow: hidden;
  width: 0;
  height: 0;
  color: transparent;
}

.js .page--inactive {
  position: absolute;
  z-index: 0;
  top: 0;
  opacity: 0;
}

/* page content */
.info {
  font-size: 1.25em;
  max-width: 50%;
  margin-top: 1.5em;
}

.poster {
  position: absolute;
  bottom: 4vh;
  left: 60%;
  max-width: 100%;
  max-height: 80%;
}

/* Menu button */
.menu-button {
  position: absolute;
  z-index: 1000;
  top: 30px;
  left: 30px;
  width: 30px;
  height: 24px;
  padding: 0;
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent;
}

.no-js .menu-button {
  display: none;
}

.menu-button::before,
.menu-button::after,
.menu-button span {
  background: #5f656f;
}

.menu-button::before,
.menu-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  pointer-events: none;
  -webkit-transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.menu-button span {
  position: absolute;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 2px;
  text-indent: 200%;
  -webkit-transition: opacity 0.25s;
  transition: opacity 0.25s;
}

.menu-button::before {
  -webkit-transform: translate3d(0, -10px, 0) scale3d(0.8, 1, 1);
  transform: translate3d(0, -10px, 0) scale3d(0.8, 1, 1);
}

.menu-button::after {
  -webkit-transform: translate3d(0, 10px, 0) scale3d(0.8, 1, 1);
  transform: translate3d(0, 10px, 0) scale3d(0.8, 1, 1);
}

.menu-button--open span {
  opacity: 0;
}

.menu-button--open::before {
  -webkit-transform: rotate3d(0, 0, 1, 45deg);
  transform: rotate3d(0, 0, 1, 45deg);
}

.menu-button--open::after {
  -webkit-transform: rotate3d(0, 0, 1, -45deg);
  transform: rotate3d(0, 0, 1, -45deg);
}

@media screen and (max-width: 60em) {
  .info {
    max-width: 100%;
  }

  .poster {
    position: relative;
    top: auto;
    left: auto;
    display: block;
    max-width: 100%;
    max-height: 50vh;
    margin: 0 0 0 50%;
  }

  .pages-nav__item {
    width: 50%;
    min-height: 20px;
  }

  .link--page {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .link--social {
    margin: 0 0.1em;
  }
}
@media screen and (max-width: 40em) {
  .js .pages-nav {
    display: block;
    padding: 10px 20px 0 20px;
    text-align: left;
  }

  .js .pages-nav__item {
    width: 100%;
    padding: 4px 0;
  }

  .js .pages-nav__item--small {
    display: inline-block;
    width: auto;
    margin-right: 5px;
  }

  .pages-nav__item--social {
    font-size: 0.9em;
  }

  .menu-button {
    top: 15px;
    right: 10px;
    left: auto;
  }

  .info {
    font-size: 0.85em;
  }

  .poster {
    margin: 1em;
  }
}
